* {
    margin: 0;
    padding: 0;
    font-family: monospace;
}

.log-nav {
    height: 70px;
    background-color: rgb(251, 251, 251);
    display: flex;
    justify-content: start;
    align-items: center;
}

.logo {
    height: 100%;
    width: 100px;
    background-image: url("../resources/logo.png");
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo-line {
    height: 100%;
    width: 160px;
    background-image: url("../resources/logo-line.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-page {
    height: 90vh;
    background-color: rgb(230, 230, 230);
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-card {
    height: 300px;
    width: 700px;
    background-color: rgb(251, 251, 251);
    z-index: 100;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.429);
}

.names {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 60px;
    width: 180px;
    padding-left: 20px;
    font-size: 1.2rem;

}

.author-login,
.user-login {
    text-decoration: none;
    color: black;
}

.tab-button {
    background: none;
    border: none;
    cursor: pointer;
    font-family: monospace;
    font-size: 1.1rem;
    color: black;
    padding: 9px;
    position: relative;
}

.tab-button::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: black;
    transform: scaleX(0);
    transform-origin: center;
    transition: transform 0.3s ease-out;
}

.tab-button.active::after {
    transform: scaleX(1);
}


.log-in {
    padding: 0 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.log-in h2 {
    margin-top: 20px;
    font-size: 1.5rem;
    color: #333;
}

.log-in form {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-top: 20px;
}

.log-in label {
    margin-bottom: 5px;
    font-size: 0.9rem;
    color: #555;
}

.log-in input {
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
}


.login-button {
    padding: 12px;
    background-color: rgb(28, 28, 28);
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.2s;
}

.login-button:hover {
    background-color: #000000;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.285);
}